@import url("https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap");
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1.3;
  width: 100%;
  min-height: 100vh;
  font-family: "Barlow Semi Condensed", sans-serif;
  background: radial-gradient(at top, #1f3756, #141539);
}

.wrapper {
  border: 1px solid white;
}

header {
  border: 2px solid #606e85;
  border-radius: 0.7rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 85%;
  max-width: 43.75rem;
  margin: 0 auto;
  margin-top: 2.125rem;
  padding: 1rem;
}
header .logo {
  width: 5.625rem;
  height: 100%;
}
@media (min-width: 769px) {
  header .logo {
    width: 10.625rem;
  }
}
header .score-container {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 5rem;
  border-radius: 0.6rem;
  padding: 1rem;
}
@media (min-width: 769px) {
  header .score-container {
    width: 9.375rem;
  }
}
header .score-container .score-text {
  color: #2a46c0;
  font-size: 0.625rem;
  letter-spacing: 1px;
  text-transform: uppercase;
}
@media (min-width: 769px) {
  header .score-container .score-text {
    font-size: 0.875rem;
  }
}
header .score-container .score-number {
  font-size: 1.875rem;
  color: #3b4363;
  font-weight: bold;
}
@media (min-width: 769px) {
  header .score-container .score-number {
    font-size: 2.9375rem;
  }
}

.game-container {
  width: 100%;
  padding: 1rem;
  max-width: 31.25rem;
  min-height: 18.125rem;
  border: 1px solid magenta;
  margin: 0 auto;
  margin-top: 6.0625rem;
  display: grid;
  grid-template-areas: "paper scissor" "rock rock";
  gap: 1rem;
  position: relative;
}
@media (min-width: 769px) {
  .game-container {
    gap: 5rem;
  }
}
.game-container::before {
  content: " ";
  position: absolute;
  width: 50%;
  height: 70%;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: url(/images/bg-triangle.svg) no-repeat;
  background-size: contain;
  z-index: -1;
}
@media (min-width: 769px) {
  .game-container::before {
    width: 100%;
    height: 70%;
    top: 55%;
    left: 75%;
  }
}
.game-container .choice-btn {
  width: 6.25rem;
  margin: 0 auto;
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
}
.game-container .choice-btn[data-armor=paper] {
  grid-area: paper;
}
.game-container .choice-btn[data-armor=scissor] {
  grid-area: scissor;
}
.game-container .choice-btn[data-armor=rock] {
  grid-area: rock;
}
.game-container .choice-img {
  position: relative;
  width: 6.25rem;
  height: 6.25rem;
  background: #fff;
  border-radius: 50%;
  display: grid;
  place-items: center;
  box-shadow: inset 0 0.36rem rgba(0, 0, 0, 0.3);
}
@media (min-width: 769px) {
  .game-container .choice-img {
    width: 8.75rem;
    height: 8.75rem;
  }
}
.game-container .choice-img::before {
  content: " ";
  position: absolute;
  left: -15%;
  top: -15%;
  width: 130%;
  height: 130%;
  border-radius: 50%;
  z-index: -1;
  box-shadow: inset 0 -0.36rem rgba(0, 0, 0, 0.4);
}
.game-container .choice-img[data-paper]::before {
  background: linear-gradient(to bottom, #4865f4, #5671f5);
}
.game-container .choice-img[data-scissor]::before {
  background: linear-gradient(to bottom, #ec9e0e, #eca922);
}
.game-container .choice-img[data-rock]::before {
  background: linear-gradient(to bottom, #dc2e4e, #dd405d);
}